<template>
	<div>	
		<template v-if="action == 'default'">
			<Alert type="success" show-icon>
		        支付成功
		        <div slot="desc">
		        	<p>您已经通过支付宝完成支付</p>
		        	<Button type="ghost" size="small" @click="gobase" icon="home">查看系统状态</Button> 
		        	<Button type="ghost" size="small" @click="gosystem" icon="log-in">登录系统</Button> 
		        </div>
		    </Alert>
		</template>
	</div>
</template>

<script>

	import common from '@/libs/common.mixin'

	export default{
		mixins:[common],
		data () {
			return {
				action:'default'
			}
		},
		methods: {
			gosystem(){
				let product_url = this.$store.state.user.info.product_url
				let arr_url = product_url.split('://')
				let url = arr_url[0]+'://'+this.$store.state.client.host+'.'+arr_url[1]
				location.href = url
			},
			gobase(){
				this.$router.replace('/base')
			}
		}
	}
</script>


